$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.wp-block-b-blocks-image-gallery {
	* {
		box-sizing: border-box;
	}

	.imgGalleryWrapper {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		justify-content: space-between;

		a.imgGalleryImgLink {
			flex-grow: 1;
			max-width: 100%;
			padding: 5px;
			position: relative;

			img.imgGalleryImg {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.imgGalleryImgCaption {
				position: absolute;
				transition: all .3s ease;
				width: calc(100% - 10px);
				height: calc(100% - 10px);
				top: 5px;
				left: 5px;
				display: flex;
				align-items: center;
				justify-content: center;
				overflow: hidden;

				&::before {
					content: '';
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
					background: rgba(256, 256, 256, .5);
					backdrop-filter: blur(20px);
					transform: scaleX(0);
					transform-origin: right;
					transition: transform .3s ease-in-out;
					transition-delay: .3s;
				}

				.captionContent {
					position: relative;
					padding: 20px;
					width: 100%;
					z-index: 1;
					transform: translateX(-100%);
					transition: transform .3s ease-in-out;
					transition-delay: 0s;

					h4 {
						color: inherit;
						font-weight: bold;
						margin: 0 0 15px 0 !important;
					}

					p {
						color: inherit;
						margin: 0 !important;
					}
				}

				.imgGalleryPreviewBtn {
					display: inline-block;
					color: #fff;
					background-color: $primaryColor;
					padding: 8px 15px;
					margin-top: 15px;
					border: none;
					border-radius: 3px;
					transition: all .3s ease;

					&:hover {
						background-color: $secondaryColor;
					}

					i {
						margin-right: 10px;
					}
				}
			}

			&:hover {
				.imgGalleryImgCaption {
					&::before {
						transform: scaleX(1);
						transform-origin: left;
						transition-delay: 0s;
					}

					.captionContent {
						transform: translateX(0);
						transition-delay: .3s;
					}
				}
			}
		}

		@for $i from 1 through 9 {
			&.columns-#{$i} {
				a.imgGalleryImgLink {
					flex-basis: calc(100% / #{$i});
				}
			}
		}

		@media (max-width: 767px) {
			@for $i from 1 through 9 {
				&.columns-tablet-#{$i} {
					a.imgGalleryImgLink {
						flex-basis: calc(100% / #{$i});
					}
				}
			}
		}

		@media (max-width: 575px) {
			@for $i from 1 through 9 {
				&.columns-mobile-#{$i} {
					a.imgGalleryImgLink {
						flex-basis: calc(100% / #{$i});
					}
				}
			}
		}
	}
}